import { useEffect, useRef, useCallback } from 'react';

const getCanvasObject = (canvas) => {
  let jsonCanvas = canvas.toJSON([
    'id',
    'cornerRadius',
    'isSvg',
    'padding',
    'cursorWidth',
    'rx',
    'ry',
    'noScaleCache',
    'crossOrigin'
  ]);
  let zoom = canvas.getZoom();
  jsonCanvas.width = canvas.getWidth() / zoom;
  jsonCanvas.height = canvas.getHeight() / zoom;
  jsonCanvas.resize = canvas.resize;
  return jsonCanvas;
};

const useCanvasUpdate = (
  isMounted,
  updateProject,
  canvas,
  info,
  dispatch,
  isDownloading,
  state
) => {
  const timerRef = useRef(null);
  const canUpdateRef = useRef(true);

  const triggerCanvasUpdate = useCallback(() => {
    if (timerRef.current) {
      clearTimeout(timerRef.current);
    }
    let timeout = setTimeout(() => {
      if (isMounted.current && canUpdateRef.current) {
        timerRef.current = null;
        let jsonCanvas = getCanvasObject(canvas);
        updateProject({
          id: info.id,
          canvas: { ...jsonCanvas },
        });
      }
    }, 1500);
    timerRef.current = timeout;
  }, [updateProject, info, canvas, isMounted]);

  useEffect(() => {
    if (state.shouldTriggerCanvasUpdate && canvas) {
      dispatch({ type: 'setShouldTriggerCanvasUpdate', data: false });
      if (!isDownloading) triggerCanvasUpdate();
    }
  }, [
    triggerCanvasUpdate,
    state.shouldTriggerCanvasUpdate,
    dispatch,
    isDownloading,
    canvas
  ]);

  useEffect(() => {
    if (state.isCropMode) canUpdateRef.current = false;
    else canUpdateRef.current = true;
  }, [state.isCropMode]);
};

export default useCanvasUpdate;
